import 'materialize-css/dist/css/materialize.min.css';
import '../main.scss';
import 'materialize-css/dist/js/materialize.min.js';


function getUrlParameter(parameterName) {
  var searchStr = decodeURIComponent(window.location.search.substring(1));
  var parameters = searchStr.split('&');

  for (var i = 0; i < parameters.length; i++) {
    var name = parameters[i].split('=');

    if (name[0] === parameterName) {
      return name[1] === undefined ? true : name[1];
    }
  }
};


var statusCode = getUrlParameter('status');
var errorDesc = '';

switch(statusCode) {
  case '404':
    errorDesc = 'Page not found!';
    break;
}

$('.status').text(statusCode);
$('.desc').text(errorDesc);


